import React from "react";
import { MDBBtn, MDBIcon, MDBBtnGroup } from "mdbreact";
import "./Contacts.css";

export const Contact = () => (
  <div class="contact_page">
    <div className="papa_contact text-center">
      <div className="texto_contact">
        <MDBBtnGroup vertical>
          <div className="contact-item">
            <div className="col">
              <MDBBtn color="indigo" social="fb">
                <MDBIcon icon="phone" className="pr-1" />
              </MDBBtn>
              <span>9(999)-999-99-99</span>
            </div>
          </div>
          <div className="contact-item">
            <div className="col">
              <MDBBtn color="yellow" social="email">
                <MDBIcon icon="envelope" />
              </MDBBtn>
              <span>gmail.com/examplemail</span>
            </div>
          </div>
          <div className="contact-item">
            <div className="col">
              <MDBBtn color="red" social="yt">
                <MDBIcon fab icon="youtube" />
              </MDBBtn>
              <span>youtube.com/examplechannel</span>
            </div>
          </div>
          <div className="contact-item">
            <div className="col">
              <MDBBtn color="indigo" social="fb">
                <MDBIcon fab icon="facebook-f" />
              </MDBBtn>
              <span>facebook.com/examplepage</span>
            </div>
          </div>
          <div className="contact-item">
            <div className="col">
              <MDBBtn color="light-blue" social="tw">
                <MDBIcon fab icon="twitter" />
              </MDBBtn>
              <span>twitter.com/examplepage</span>
            </div>
          </div>
          <div className="contact-item">
            <div className="col">
              <MDBBtn color="light-green" social="fb">
                <MDBIcon fab icon="whatsapp" />
              </MDBBtn>
              <span>9(999)-999-99-99</span>
            </div>
          </div>
        </MDBBtnGroup>
      </div>
    </div>
  </div>
);
